import { useState, useEffect } from "react";

// react-router components
import { Switch, Redirect, useLocation } from "react-router-dom";

// @mui material components
import { ThemeProvider } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import Icon from "@mui/material/Icon";

// Vision UI Dashboard React components
import VuiBox from "./components/VuiBox";

// Vision UI Dashboard React example components
import Sidenav from "@tg/examples/Sidenav";
import Configurator from "@tg/examples/Configurator";

// Vision UI Dashboard React themes
import theme from "./assets/theme";
import Content from "@tg/layouts/create";

// Vision UI Dashboard React routes
import routes from "./routes";

// Vision UI Dashboard React contexts
import { useVisionUIController, setMiniSidenav, setOpenConfigurator } from "./context";

export default function App() {
  const [controller, dispatch] = useVisionUIController();
  const { miniSidenav, direction, openConfigurator, sidenavColor } = controller;
  const [onMouseEnter, setOnMouseEnter] = useState(false);
  const { pathname } = useLocation();
  const [routeData, setRouteData] = useState('create');

  // const routeData = useMemo(() => routes.filter((route) => {
  //   if (pathname === '/select') {
  //     return route.show === true || route.route === pathname;
  //   }
  //   if (pathname === '/show') {
  //     return route;
  //   }

  //   return route.show === true;
  // }), [routes]);


  // Open sidenav when mouse enter on mini sidenav
  const handleOnMouseEnter = () => {
    if (miniSidenav && !onMouseEnter) {
      setMiniSidenav(dispatch, false);
      setOnMouseEnter(true);
    }
  };

  // Close sidenav when mouse leave mini sidenav
  const handleOnMouseLeave = () => {
    if (onMouseEnter) {
      setMiniSidenav(dispatch, true);
      setOnMouseEnter(false);
    }
  };

  // Change the openConfigurator state
  const handleConfiguratorOpen = () => setOpenConfigurator(dispatch, !openConfigurator);

  // Setting the dir attribute for the body element
  useEffect(() => {
    document.body.setAttribute("dir", direction);
  }, [direction]);

  // Setting page scroll to 0 when changing the route
  useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
  }, [pathname]);

  const configsButton = (
    <VuiBox
      display="flex"
      justifyContent="center"
      alignItems="center"
      width="3.5rem"
      height="3.5rem"
      bgColor="info"
      shadow="sm"
      borderRadius="50%"
      position="fixed"
      right="2rem"
      bottom="2rem"
      zIndex={99}
      color="white"
      sx={{ cursor: "pointer" }}
      onClick={handleConfiguratorOpen}
    >
      <Icon fontSize="default" color="inherit">
        settings
      </Icon>
    </VuiBox>
  );

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      {(
        <>
          <Sidenav
            color={sidenavColor}
            brand=""
            brandName="TEST GUARDIAN"
            routes={routes}
            onMouseEnter={handleOnMouseEnter}
            onMouseLeave={handleOnMouseLeave}
            routeData={routeData}
            setRouteData={setRouteData}
          />
          <Configurator />
          {configsButton}
        </>
      )}
      <Switch>
        <Content routeData={routeData} setRouteData={setRouteData} />
        <Redirect from="*" to="/" />
      </Switch>
    </ThemeProvider>
  );
}
