import { useState, useRef } from "react";
import PropTypes from "prop-types";

// @mui material components
import Card from "@mui/material/Card";
import CircularProgress from '@mui/material/CircularProgress';

// Vision UI Dashboard React components
import VuiBox from "@tg/components/VuiBox";
import VuiTypography from "@tg/components/VuiTypography";

// Vision UI Dashboard React example components
import DashboardLayout from "@tg/examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "@tg/examples/Navbars/DashboardNavbar";
import Table from "@tg/examples/Tables/Table";
import VuiInput from "@tg/components/VuiInput";
import VuiButton from "@tg/components/VuiButton";
import MuiCheckBox from "@mui/material/Checkbox";


function Create({ routeData, setRouteData }) {
  //-------data------//
  const aspectData = [{
    name: 'Option',
    subName: 'Installer',
    description: 'For example, testing valid and invalid arguments for the installation, activation cose, and admin password.',
    checked: true,
  }, {
    name: 'Path',
    subName: 'Installer',
    description: 'For example, testing the installation directory path for different drives and folder structures, and testing ling and short paths.',
    checked: true
  }, {
    name: 'OS',
    subName: 'Installer',
    description: 'For example, testing the installation on diffrernt opeating systems, and testing the installation with different user account types.',
    checked: false
  }, {
    name: 'Setup',
    subName: 'Installer',
    description: 'For example, testing the installation of divers, services, and files, and testing the installation of shortcuts and registry entries.',
    checked: true
  }];
  const [checkState, setCheckState] = useState(aspectData.map(item => item.checked));
  const prCols = [
    { name: "check", display: "check", align: "center" },
    { name: "aspect", display: "test aspect", align: "left" },
    { name: "description", display: "description", align: "left" },
  ];
  const prRows = aspectData.map((item, index) => {
    return {
      check: (
        <VuiBox display="flex" alignItems="center">
          <VuiTypography pl="16px" color="white" variant="button" fontWeight="medium">
            <MuiCheckBox
              style={{ border: "1px solid rgba(255, 255, 255, 0.2)" }}
              checked={checkState[index]}
              onChange={(e) => {
                const newCheckState = [...checkState];
                newCheckState[index] = e.target.checked;
                setCheckState(newCheckState);
              }}
            />
          </VuiTypography>
        </VuiBox>
      ),
      aspect: (
        <VuiBox display="flex" flexDirection="column">
          <VuiTypography variant="caption" fontWeight="medium" color="white">
            {item.name}
          </VuiTypography>
          <VuiTypography variant="caption" color="text">
            ({item.subName})
          </VuiTypography>
        </VuiBox>
      ),
      description: (
        <VuiTypography variant="caption" color="text">
          {item.description}
        </VuiTypography>
      ),
    }
  });
  const [showLoading, setShowLoading] = useState(false);
  const [fileName, setFileName] = useState('');
  const checkMissedAspects = () => {
    setShowLoading(true);
    setTimeout(() => {
      // todo: call api
      setRouteData('select');
      setShowLoading(false);
    }, 3000);
  }
  const showReviewDetail = () => {
    setShowLoading(true);
    setTimeout(() => {
      // todo: call api
      setRouteData('show');
      setShowLoading(false);
    }, 3000);
  }
  const exportAsText = () => {
    const text = textAreaRef.current.innerText;
    const blob = new Blob([text], { type: "text/plain" });
    const url = URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.download = "review.txt";
    link.href = url;
    link.click();
  }
  const hiddenFileInput = useRef(null);
  const textAreaRef = useRef(null);

  const handleClick = event => {
    hiddenFileInput.current.click();
  };

  const handleChange = event => {
    const fileUploaded = event.target.files[0];
    setFileName(fileUploaded.name);
    const reader = new FileReader()
    reader.onload = async (fileUploaded) => { 
      const text = (fileUploaded.target.result);
      console.log(text)
    };
  };


  return (
    <DashboardLayout>
      <DashboardNavbar routeData={routeData} />
      <VuiBox py={3}>
        <VuiBox mb={3} display={routeData === 'create' ? 'block' : 'none'}>
          <Card>
            <VuiBox display="flex" justifyContent="space-between" alignItems="center" mb="22px">
              <VuiTypography variant="lg" color="white">
                Select test case
              </VuiTypography>
            </VuiBox>
            <VuiBox>
              <VuiTypography variant="button" color="text" fontWeight="regular">
                Open text / excel file
              </VuiTypography><br />
              <VuiButton
                variant="contained"
                size="small"
                color="primary"
                onClick={handleClick}
              >
                Choose a file
              </VuiButton>
              <input
                type="file"
                ref={hiddenFileInput}
                style={{display:'none'}}
                accept=".xls,.xlsx,.text"
                onChange={handleChange}
              />
              <VuiTypography variant="button" color="white" fontWeight="regular" ml={1}>
                {fileName}
              </VuiTypography>
            </VuiBox>
            <br/>
            <VuiBox>
              <VuiTypography variant="button" color="text" fontWeight="regular">
                Past test case
              </VuiTypography>
            </VuiBox>
            <VuiBox>
            <VuiInput placeholder="Past here..." multiline rows={5} />
            <br/>
            <VuiButton
              variant="contained"
              size="large"
              color="primary"
              onClick={checkMissedAspects}
            >
                Check missed aspects
              </VuiButton>
            </VuiBox>
          </Card>
        </VuiBox>
        <VuiBox mb={3} display={routeData === 'select' ? 'flex' : 'none'}>
          <Card>
            <VuiBox display="flex" justifyContent="space-between" alignItems="center">
              <VuiTypography variant="lg" color="white">
                Seems you have following test aspect missing
              </VuiTypography>
            </VuiBox>
            <VuiBox>
              <VuiTypography variant="button" color="text" fontWeight="regular">
                You can select and view more detail.
              </VuiTypography><br />
            </VuiBox>
            <VuiBox
              mb={3}
              sx={{
                "& th": {
                  borderBottom: ({ borders: { borderWidth }, palette: { grey } }) =>
                    `${borderWidth[1]} solid ${grey[700]}`,
                },
                "& .MuiTableRow-root:not(:last-child)": {
                  "& td": {
                    borderBottom: ({ borders: { borderWidth }, palette: { grey } }) =>
                      `${borderWidth[1]} solid ${grey[700]}`,
                  },
                },
              }}
            >
              <Table columns={prCols} rows={prRows} />
            </VuiBox>
            <VuiBox>
              <VuiButton
                variant="contained"
                size="large"
                color="primary"
                onClick={showReviewDetail}
              >
                Show reviewed detail
              </VuiButton>
            </VuiBox>
          </Card>
        </VuiBox>
        <VuiBox mb={3} display={routeData === 'show' ? 'flex' : 'none'}>
          <Card ref={textAreaRef}>
            <VuiBox display="flex" justifyContent="space-between" alignItems="center" mb="22px">
              <VuiTypography variant="lg" color="white">
                Here is detail of each test aspect
              </VuiTypography>
            </VuiBox>
            <VuiBox mb={3}>
              <VuiTypography variant="button" color="text" fontWeight="regular">
                Following may be missing from the test case.
              </VuiTypography><br />
            </VuiBox>
            <VuiBox mb={3}>
              <VuiBox>
                <VuiTypography variant="lg" color="white">
                  Option
                </VuiTypography>
              </VuiBox>
              <VuiTypography variant="button" color="white" fontWeight="regular">
                For Option test aspect, you might want to consider:
                <ul
                  style={{
                    'margin-left': '22px'
                  }}
                >
                  <li>Testing the installer with valid and invalid command line arguments.</li>
                  <li>Testing the activation code and admin password fields with valid and invalid inputs.</li>
                  <li>Testing the installer with different combinations of arguments.</li>
                </ul>
              </VuiTypography>

            </VuiBox>
            <VuiBox mb={3}>
              <VuiBox>
                <VuiTypography variant="lg" color="white">
                  Path
                </VuiTypography>
              </VuiBox>
              <VuiTypography variant="button" color="white" fontWeight="regular">
                For Path test aspect, you might want to consider:
                <ul
                  style={{
                    'margin-left': '22px'
                  }}
                >
                  <li>Testing the installation directory path for different drives and folder structures, such as a path that has non-ASCII characters, a path that exceeds the maximum number of characters allowed, and a path that includes spaces or special characters.</li>
                  <li>Testing the installer with different drives, such as removable drive, a UNC path, and a mapped drive.</li>
                  <li>Testing the installer with a path that requires authentication, such as a network path that requires a username and password.</li>
                </ul>
              </VuiTypography>

            </VuiBox>
            <VuiBox mb={3}>
              <VuiBox>
                <VuiTypography variant="lg" color="white">
                  Setup
                </VuiTypography>
              </VuiBox>
              <VuiTypography variant="button" color="white" fontWeight="regular">
                Setup test aspect, you might want to consider:
                <ul
                  style={{
                    'margin-left': '22px'
                  }}
                >
                  <li>Testing the installation of drivers, services, and files, such as testing if the files are installed in the correct location and if the drivers and services are registered correctly.</li>
                </ul>
              </VuiTypography>
            </VuiBox>
            <VuiBox>
              <VuiButton
                variant="contained"
                size="large"
                color="primary"
                onClick={exportAsText}
              >
                Export as Text
              </VuiButton>
            </VuiBox>
          </Card>
        </VuiBox>
        <VuiBox
          display={showLoading ? "flex" : "none"}
          justifyContent="center"
          alignItems="center"
          position="fixed"
          top="0"
          left="0"
          width="100%"
          height="100%"
          zIndex={9999}
          bgColor="rgba(0,0,0,0.5)"
          flexDirection="column"
        >
          <CircularProgress style={{
            'color': '#7818ee',
            'margin': '0 auto 20px',
            'display': 'block'
          }} />
          <VuiTypography variant="button" color="white" fontWeight="regular">
            AI is working, please wait for a while...
          </VuiTypography>
        </VuiBox>
      </VuiBox>
    </DashboardLayout>
  );
}

Create.propTypes = {
  routeData: PropTypes.string,
  setRouteData: PropTypes.func,
};

export default Create;
