import { useState, useEffect } from "react";

// @mui material components
import Divider from "@mui/material/Divider";
import Icon from "@mui/material/Icon";
import MuiTabs from "@mui/material/Tabs";
import MuiTab from "@mui/material/Tab";
import MuiSlider from "@mui/material/Slider";

// Vision UI Dashboard React components
import VuiBox from "@tg/components/VuiBox";
import VuiTypography from "@tg/components/VuiTypography";
import VuiButton from "@tg/components/VuiButton";
import MuiCheckBox from "@mui/material/Checkbox";
import VuiInput from "@tg/components/VuiInput";

// Custom styles for the Configurator
import ConfiguratorRoot from "@tg/examples/Configurator/ConfiguratorRoot";
import Table from "@tg/examples/Tables/Table";

// Vision UI Dashboard React context
import {
  useVisionUIController,
  setOpenConfigurator,
} from "@tg/context";

function Configurator() {
  const [controller, dispatch] = useVisionUIController();
  const { openConfigurator, transparentSidenav } = controller;
  const [disabled, setDisabled] = useState(false);
  const [tabValue, setTabValue] = useState("template");

  // Use the useEffect hook to change the button state for the sidenav type based on window size.
  useEffect(() => {
    // A function that sets the disabled state of the buttons for the sidenav type.
    function handleDisabled() {
      return window.innerWidth > 1200 ? setDisabled(false) : setDisabled(true);
    }

    // The event listener that's calling the handleDisabled function when resizing the window.
    window.addEventListener("resize", handleDisabled);

    // Call the handleDisabled function to set the state with the initial value.
    handleDisabled();

    // Remove event listener on cleanup
    return () => window.removeEventListener("resize", handleDisabled);
  }, []);

  const handleCloseConfigurator = () => setOpenConfigurator(dispatch, false);
  const handleChange = (_event, newValue) => {
    setTabValue(newValue === 0 ? "template" : "parameter");
  };

  // sidenav type buttons styles
  const sidenavTypeButtonsStyles = ({
    functions: { pxToRem },
    boxShadows: { buttonBoxShadow },
  }) => ({
    height: pxToRem(42),
    boxShadow: buttonBoxShadow.main,

    "&:hover, &:focus": {
      opacity: 1,
    },
  });
  const templateData = [{
    name: 'test_template_installer',
    checked: true,
  }, {
    name: 'test_template_UI',
    checked: true,
  }, {
    name: 'test_template_SCAN',
    checked: true,
  }, {
    name: 'test_template_Browser',
    checked: false,
  }, {
    name: 'test_template_Performance',
    checked: true,
  }, {
    name: 'test_template_Security',
    checked: true,
  }];
  const [templateCheckState, setTemplateCheckState] = useState(templateData.map((item) => item.checked));
  const prCols = [
    { name: "check", display: "", align: "center" },
    { name: "aspect", display: "test template", align: "left" },
    { name: "description", display: "description", align: "left" },
  ];
  const prRows = templateData.map((item, i) => {
    return {
      check: (
        <VuiBox display="flex" alignItems="center">
          <VuiTypography pl="16px" color="white" variant="button" fontWeight="medium">
            <MuiCheckBox
              style={{ border: "1px solid rgba(255, 255, 255, 0.2)" }}
              checked={templateCheckState[i]}
              onChange={(e) => {
                const newCheckState = [...templateCheckState];
                newCheckState[i] = e.target.checked;
                setTemplateCheckState(newCheckState);
              }}
            />
          </VuiTypography>
        </VuiBox>
      ),
      aspect: (
        <VuiBox display="flex" flexDirection="column">
          <VuiTypography variant="caption" fontWeight="medium" color="white">
            {item.name}
          </VuiTypography>
        </VuiBox>
      ),
      description: null,
    }
  });
  const parameterData = [{
    name: 'temperature',
    value: 0.7,
  }, {
    name: 'topProbabilities',
    value: 0.95,
  }, {
    name: 'stopSequences',
    value: null,
  }, {
    name: 'pastMessagesToInclude',
    value: 3,
  }, {
    name: 'frequencyPenalty',
    value: 0,
  }, {
    name: 'presencePenalty',
    value: 0,
  }];
  const parameterCols = [
    { name: "max", display: "max response length", align: "left" },
    { name: "number", display: "800", align: "center" },
  ];
  const parameterRows = parameterData.map((item) => {
    return {
      max: (
        <VuiBox display="flex" alignItems="center">
          <VuiTypography variant="caption" fontWeight="medium" color="white">
            {item.name}
          </VuiTypography>
        </VuiBox>
      ),
      number: (
        <VuiBox display="flex" flexDirection="column">
          <VuiTypography variant="caption" fontWeight="medium" color="white">
            <VuiInput
              type="number"
              value={item.value}
              onChange={(e) => {
                console.log(e.target.value);
              }}
              sx={{
                "& input": {
                  textAlign: "center",
                },
              }}
            />
          </VuiTypography>
        </VuiBox>
      ),
    }
  });

  return (
    <ConfiguratorRoot variant="permanent" ownerState={{ openConfigurator }}>
      <VuiBox
        backgroundColor="black"
        display="flex"
        justifyContent="space-between"
        alignItems="baseline"
        pt={3}
        pb={0.8}
        px={3}
      >
        <VuiBox>
          <VuiTypography color="white" variant="h5" fontWeight="bold">
            Test Guardian Configurator
          </VuiTypography>
          <VuiTypography variant="body2" color="white" fontWeight="bold">
            See our test review options.
          </VuiTypography>
        </VuiBox>

        <Icon
          sx={({ typography: { size, fontWeightBold }, palette: { white, dark } }) => ({
            fontSize: `${size.md} !important`,
            fontWeight: `${fontWeightBold} !important`,
            stroke: `${white.main} !important`,
            strokeWidth: "2px",
            cursor: "pointer",
            mt: 2,
          })}
          onClick={handleCloseConfigurator}
        >
          close
        </Icon>
      </VuiBox>

      <Divider light />

      <VuiBox pt={1.25} pb={3} px={3}>
        <MuiTabs
          sx={{ borderBottom: 2, borderColor: '#6655a4', borderRadius: 0, background: 'transparent' }}
          value={tabValue === "template" ? 0 : 1}
          onChange={handleChange}
        >
          <MuiTab label="Test Template Management" />
          <MuiTab label="Parameter"  />
        </MuiTabs>

        <VuiBox mt={3} lineHeight={1} sx={{ display: tabValue !== "template" && "none" }}>
          <VuiTypography variant="h6" color="white">
            Available Test Templates
          </VuiTypography>
          <VuiTypography variant="button" color="text" fontWeight="regular">
            Select templates which you want to use for review.<br />
            You can export selected test template(s) by &quot;Export Selected Test Template&quot; button.
          </VuiTypography>
          <VuiBox
            mt={3}
            mb={5}
            sx={{
              "& th": {
                borderBottom: ({ borders: { borderWidth }, palette: { grey } }) =>
                  `${borderWidth[1]} solid ${grey[700]}`,
              },
              "& .MuiTableRow-root:not(:last-child)": {
                "& td": {
                  borderBottom: ({ borders: { borderWidth }, palette: { grey } }) =>
                    `${borderWidth[1]} solid ${grey[700]}`,
                },
              },
            }}
          >
            <Table columns={prCols} rows={prRows} />
          </VuiBox>
          <VuiBox
            sx={{
              display: "flex",
              mt: 2,
              mb: 5,
            }}
          >
            <VuiButton
              color="info"
              variant={transparentSidenav ? "outlined" : "contained"}
              // onClick={handleWhiteSidenav}
              disabled={disabled}
              fullWidth
              sx={sidenavTypeButtonsStyles}
            >
              Import test Template
            </VuiButton>
            <VuiButton
              color="info"
              variant={transparentSidenav ? "outlined" : "contained"}
              // onClick={handleWhiteSidenav}
              disabled={disabled}
              fullWidth
              sx={{
                ml: 1
              }}
            >
              Export Selected Test Template
            </VuiButton>
            <VuiButton
              color="info"
              variant={transparentSidenav ? "outlined" : "contained"}
              // onClick={handleWhiteSidenav}
              disabled={disabled}
              fullWidth
              sx={{
                ml: 1
              }}
            >
              Remove Selected Test Template
            </VuiButton>
          </VuiBox>
          <VuiBox
            sx={{
              display: "flex",
              mt: 2,
              mb: 5,
            }}
          >
            <VuiButton
              color="info"
              variant={transparentSidenav ? "contained" : "outlined"}
              // onClick={handleTransparentSidenav}
              disabled={disabled}
              sx={{
                ml: 1,
                ...sidenavTypeButtonsStyles,
              }}
            >
              Apply
            </VuiButton>
          </VuiBox>
        </VuiBox>

        <VuiBox mt={3} lineHeight={1}  sx={{ display: tabValue !== "parameter" && "none" }}>
          <VuiBox mb={3}>
            <VuiTypography variant="button" color="text">
              Azure chatParameters and other parameters.
            </VuiTypography>
          </VuiBox>
          <VuiTypography variant="h6" color="white">
            Review randomness
          </VuiTypography>
          <VuiBox display="flex" justifyContent="space-between" alignItems="center" mt={3}>
            <VuiBox display="flex">
              <VuiTypography color="white" fontWeight="regular" variant="button">
                Deterministically
              </VuiTypography>
            </VuiBox>
            <VuiBox display="flex">
              <VuiTypography color="white" fontWeight="regular" variant="button">
                Dreativety
              </VuiTypography>
            </VuiBox>
          </VuiBox>
          <VuiBox mb={3}>
            <MuiSlider
              aria-label="Temperature"
              defaultValue={33}
              step={33}
              // marks
              // min={10}
              max={99}
            />
          </VuiBox>

          {/* <MuiSlider defaultValue={30} step={10} marks min={10} max={110} disabled /> */}
          <VuiTypography variant="h6" color="white">
            Other parameters
          </VuiTypography>
          <VuiBox
            mt={3}
            mb={5}
            sx={{
              "& th": {
                borderBottom: ({ borders: { borderWidth }, palette: { grey } }) =>
                  `${borderWidth[1]} solid ${grey[700]}`,
              },
              "& .MuiTableRow-root:not(:last-child)": {
                "& td": {
                  borderBottom: ({ borders: { borderWidth }, palette: { grey } }) =>
                    `${borderWidth[1]} solid ${grey[700]}`,
                },
              },
            }}
          >
            <Table columns={parameterCols} rows={parameterRows} />
          </VuiBox>
          <VuiBox
            sx={{
              display: "flex",
              mt: 2,
              mb: 5,
            }}
          >
            <VuiButton
              color="info"
              variant={transparentSidenav ? "contained" : "outlined"}
              // onClick={handleTransparentSidenav}
              disabled={disabled}

              sx={{
                ml: 1,
                ...sidenavTypeButtonsStyles,
              }}
            >
              Apply
            </VuiButton>
          </VuiBox>
        </VuiBox>
      </VuiBox>
    </ConfiguratorRoot>
  );
}

export default Configurator;
